import React, { PureComponent } from 'react';

import { Formik, Form } from 'formik';
import md5 from 'blueimp-md5';

import Box from 'components/Box';
import Text from 'components/Text';
import Container from 'components/Container';
import Button from 'components/Button';
import Flex from 'components/Flex';
import Input from 'components/Input';
import { mobileOrDesktop } from 'components/ThemeProvider/theme'

import Title from '../Title'
import { askings } from '../../NoticePage'

const nameId = askings.map((name) => md5(name))
const initialValue = nameId.reduce((iv, name) => Object.assign(iv, { [name]: '' }), {})

class Note extends PureComponent {
  state = {}

  handleConfirm = () => {
    this.setState({ confirm: true })
  }

  handleModify = () => {
    this.setState({ confirm: false })
  }

  render() {
    const { notes, onStore } = this.props
    const { confirm } = this.state
    if (confirm) {
      return (
        <Box
          mx={mobileOrDesktop('1em', '0.5em')}
          py="2em"
          boxShadow="-2px 3px 2px rgba(0,0,0,0.1)"
          bg="white"
          borderRadius="0 0 1em 1em"
        >
          <Title mt="2em" title="我的看診回饋" />
          <Container>
            <Flex flexWrap="wrap">
              {askings.map((a, i) => {
                const name = nameId[i]
                return (
                  <Box width={1/3} mt="1em" key={i}>
                    <Box mx="1em">
                      <Text my="0.5em">{a}</Text>
                      <Text fontSize="1.5em">{notes[name]}</Text>
                    </Box>
                  </Box>
                )})}
            </Flex>
            <Box textAlign="center" mt="1em">
              <Button onClick={this.handleModify}>修改</Button>
            </Box>
          </Container>
        </Box>
      )
    }
    return (
      <Box
        mx={mobileOrDesktop('1em', '0.5em')}
        py="2em"
        boxShadow="-2px 3px 2px rgba(0,0,0,0.1)"
        bg="white"
        borderRadius="0 0 1em 1em"
      >
        <Title mt="2em" title="我的看診回饋" />
        <Container>
          <Formik
            initialValues={Object.assign({}, initialValue, notes)}
            onSubmit={() => {
              this.handleConfirm()
            }}
          >
            {({ handleChange, values }) => console.log(values) || (
              <Form>
                <Flex flexWrap="wrap">
                  {askings.map((a, i) => {
                    const name = nameId[i]
                    return (
                      <Box width={1/3} mt="1em" key={i}>
                        <Box mx="1em">
                          <Text my="0.5em">{a}</Text>
                          <Input
                            placeholder="請輸入"
                            onChange={(e) => {
                              handleChange(e)
                              onStore(e.target)
                            }}
                            name={name}
                            value={values[name]}
                          />
                        </Box>
                      </Box>
                    )})}
                </Flex>
                <Box textAlign="center" mt="1em">
                  <Button type="submit">儲存</Button>
                </Box>
              </Form>
            )}
          </Formik>
        </Container>
      </Box>
    );
  }
}

export default Note;
