import React, { useState } from 'react';
import { IoMdArrowDropleft, IoMdArrowDropright } from 'react-icons/io'
import format from 'date-fns/format'
import { navigate } from 'gatsby'

import Circle from 'components/Circle';
import Text from 'components/Text';
import Flex from 'components/Flex';
import Button from 'components/Button';
import Container from 'components/Container';

import Note from './Note';
import Memo from './Memo';
import storingHoc from '../storingHoc'

const ViewHistory = ({ hid, memos, storeNote }) => {
  const [show, setShow] = useState(0)
  const memoId = hid - 1
  const memo = memos[memoId]

  return (
    <Container py="2em">
      <Flex justifyContent="center" alignItems="center">
        <Circle
          width="1.5em"
          is={p => <Button.circle disabled={!memoId} {...p} />}
          onClick={() => navigate(`/memo/history/${memoId}`)}
        >
          <IoMdArrowDropleft size="20" />
        </Circle>
        <Text mx="0.5em" my="1em" color="titleBlue" fontSize="1.5em" fontWeight="bold">{format(memos[hid - 1].createdAt, 'YYYY/MM/DD')}</Text>
        <Circle
          width="1.5em"
          is={p => <Button.circle disabled={memoId === memos.length - 1} {...p} />}
          onClick={() => navigate(`/memo/history/${memoId + 2}`)}
        >
          <IoMdArrowDropright size="20" />
        </Circle>
      </Flex>
      <Flex mx="0.5em">
        <Button.tabs width={1} active={!show} onClick={() => setShow(0)}>看診備忘錄</Button.tabs>
        <Button.tabs width={1} active={show} onClick={() => setShow(1)}>紀錄醫生回饋</Button.tabs>
      </Flex>
      {Boolean(show) ? (
        <Note
          notes={memo.notes}
          onStore={({ name, value }) => storeNote({ index: memoId, name, value })}
        />
      ) : <Memo memo={memo} onEdit={() => navigate(`/memo/history/${hid}/edit/1`)} />}
    </Container>
  );
};

export default storingHoc(ViewHistory);
